<script type="text/ecmascript-6">
    import $ from 'jquery'

    export default {
        // props: {
        //   title: {
        //     type: String,
        //     default: '正在载入...'
        //   }
        // }
        data() {
            return {
                count: 1,
                sec: 1,
                mDay: new Date(2019, 5, 23).getTime(),
                nDay: new Date().getTime(),
                secondTimer: null,
                minuteTimer: null,
                hourTimer: null,
                secondOnce: null,
                minuteOnce: null,
                hourOnce: null
            }
        },
        computed: {
            diffDay: function () {
                return Math.floor((this.nDay - this.mDay) / (24 * 60 * 60 * 1000))
            }
        },
        mounted() {
            this.monitorScreen()
        },
        methods: {
            monitorScreen() {
                document.onvisibilitychange = () => {
                    if (document.visibilityState === "visible" && this.sec <= this.diffDay) {

                        this.secondTimer = setInterval(() => {
                            this.secondPlay()
                        }, 600);

                        this.minuteOnce = setTimeout(() => {
                            this.minutePlay();
                            this.minuteTimer = setInterval(() => {
                                this.minutePlay()
                            }, 6000);
                        }, (10 - (this.sec - 1) % 10) * 600);

                        this.hourOnce = setTimeout(() => {
                            this.hourPlay();
                            this.hourTimer = setInterval(() => {
                                this.hourPlay()
                            }, 60000);
                        }, (100 - this.sec % 100 + 1) * 600)
                    } else {
                        clearInterval(this.secondTimer);
                        clearInterval(this.minuteTimer);
                        clearInterval(this.hourTimer);
                        clearTimeout(this.minuteOnce);
                        clearTimeout(this.hourOnce)
                    }
                }
            },
            loadingFinish() {
                $('.mask').css({ 'opacity': 0 });
                $('.circle').css({ 'animation': 'none' });
                this.secondTimer = setInterval(() => {
                    this.secondPlay()
                }, 600);

                this.minuteTimer = setInterval(() => {
                    this.minutePlay()
                }, 6000);

                this.hourTimer = setInterval(() => {
                    this.hourPlay()
                }, 60000);
            },
            hourPlay() {
                if (this.sec === this.diffDay) {
                    clearInterval(this.secondTimer)
                    clearInterval(this.minuteTimer)
                    clearInterval(this.hourTimer)
                    $(".img").attr("src", "../img/WechatIMG12.jpg")
                    return;
                }

                $("body").removeClass("play");
                var aa = $("ul.hourPlay li.active");

                if (aa.html() == undefined) {
                    aa = $("ul.hourPlay li").eq(0);
                    aa.addClass("before")
                        .removeClass("active")
                        .next("li")
                        .addClass("active")
                        .closest("body")
                        .addClass("play");

                } else if (aa.is(":last-child")) {
                    $("ul.hourPlay li").removeClass("before");
                    aa.addClass("before").removeClass("active");
                    aa = $("ul.hourPlay li").eq(0);
                    aa.addClass("active")
                        .closest("body")
                        .addClass("play");
                } else {
                    $("ul.hourPlay li").removeClass("before");
                    aa.addClass("before")
                        .removeClass("active")
                        .next("li")
                        .addClass("active")
                        .closest("body")
                        .addClass("play");
                }
            },

            minutePlay() {
                if (this.sec === this.diffDay) {
                    clearInterval(this.secondTimer)
                    clearInterval(this.minuteTimer)
                    clearInterval(this.hourTimer)
                    $(".img").attr("src", "./img/WechatIMG12.jpg")
                    return;
                }

                $("body").removeClass("play");
                var aa = $("ul.minutePlay li.active");

                if (aa.html() == undefined) {
                    aa = $("ul.minutePlay li").eq(0);
                    aa.addClass("before")
                        .removeClass("active")
                        .next("li")
                        .addClass("active")
                        .closest("body")
                        .addClass("play");

                } else if (aa.is(":last-child")) {
                    $("ul.minutePlay li").removeClass("before");
                    aa.addClass("before").removeClass("active");
                    aa = $("ul.minutePlay li").eq(0);
                    aa.addClass("active")
                        .closest("body")
                        .addClass("play");
                } else {
                    $("ul.minutePlay li").removeClass("before");
                    aa.addClass("before")
                        .removeClass("active")
                        .next("li")
                        .addClass("active")
                        .closest("body")
                        .addClass("play");
                }
            },

            secondPlay() {
                if (this.sec % 8 === 0) {
                    $(".old-img")
                    // .attr("src", "./img/WechatIMG" + (Math.min(12, count) - 1) + ".jpg")
                        .css({ "opacity": 1, "transition": "none" });
                    $(".new-img").attr("src", "./img/WechatIMG" + Math.min(12, this.count) + ".jpg")
                    this.count++;
                } else {
                    $(".old-img").css({ "opacity": 0, "transition": "opacity 1.4s ease-out" })
                }

                if (this.sec - 1 === this.diffDay) {
                    clearInterval(this.secondTimer)
                    clearInterval(this.minuteTimer)
                    clearInterval(this.hourTimer)
                    // $(".img").attr("src", "./img/WechatIMG12.jpg")
                    return;
                }

                $("body").removeClass("play");
                var aa = $("ul.secondPlay li.active");

                if (aa.html() == undefined) {
                    aa = $("ul.secondPlay li").eq(0);
                    aa.addClass("before")
                        .removeClass("active")
                        .next("li")
                        .addClass("active")
                        .closest("body")
                        .addClass("play");

                } else if (aa.is(":last-child")) {
                    $("ul.secondPlay li").removeClass("before");
                    aa.addClass("before").removeClass("active");
                    aa = $("ul.secondPlay li").eq(0);
                    aa.addClass("active")
                        .closest("body")
                        .addClass("play");
                } else {
                    $("ul.secondPlay li").removeClass("before");
                    aa.addClass("before")
                        .removeClass("active")
                        .next("li")
                        .addClass("active")
                        .closest("body")
                        .addClass("play");
                }
                this.sec++;
            }
        }
    }
</script>
